 @mixin breakpoint($point) {
  @if $point ==desktop {
    @media (min-width: 1200px) {
      @content;
    }
  }

  @else if $point ==tablet {
    @media (min-width: 768px) and (max-width: 1199px) {
      @content;
    }
  }

  @else if $point ==mobile {
    @media (max-width: 767px) {
      @content;
    }
  }
}

@mixin responsive() {

  @media (min-width: 0px) and (max-width: 1199px) {
    @content;
  }


}